:root {
  --clr-white: hsl(0, 0%, 100%);
  --clr-lightgray: hsl(212, 45%, 89%);
  --clr-grayishblue: hsl(220, 15%, 55%);
  --clr-darkblue: hsl(218, 44%, 22%);
  --font-primary: "Outfit", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.container {
  height: 100vh;
  background-color: var(--clr-lightgray);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  font-family: var(--font-primary);
}
.container p {
  font-size: 0.9375rem;
}

.card {
  background-color: var(--clr-white);
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 20rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
  box-shadow: 0 0 2rem -1rem rgba(0, 0, 0, 0.35);
}
.card__qrcode {
  margin-bottom: 0.75rem;
}
.card__qrcode img {
  max-width: 100%;
  border-radius: 1rem;
}
.card__title {
  max-width: 100%;
  color: var(--clr-darkblue);
  font-weight: 700;
  padding: 0 1rem;
  font-size: 0.9375rem;
}
.card__title h2 {
  margin-bottom: 0.75rem;
}
.card__description {
  max-width: 100%;
  color: var(--clr-grayishblue);
  font-weight: 400;
  padding: 0 1rem;
  margin-bottom: 1.125rem;
}